.Note {
  margin: 0 20px;

  &__title {
    margin: 0;
  }

  &__content {
    text-align: left;
    font-size: 12px;
    width: 100%;
    height: 100px;
    border: 1px solid #000000;
    padding: 10px;
    margin: 10px 0;
    box-sizing: border-box;
    overflow: scroll;
    word-wrap: normal;
  }

  &__button {
    width: 100%;
    height: 30px;
  }
}